
.page_contacts .contact-border {
  height: 100%;
  min-height: 96vh;
  border: 1px solid rgba(255, 255, 255, 0.5);
  margin: 2vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.page_contacts .contact__subtitle {
  font-size: 20px;
  line-height: 48px;
  margin-top: 35px;
}
.page_contacts .contact__text {
  max-width: 650px;
  margin: 30px auto 0 auto;
}
.page_contacts .button__form {
  margin: 30px auto;
  height: 50px;
  font-size: 12px;
  line-height: 12px;
}
.page_contacts .contact-form {
  margin-top: 55px;
  padding: 0 10%;
}
.page_contacts .contact-map .map {
  width: 1px;
  opacity: 1;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 10;
  height: 100%;
}
.page_contacts .contact-map .map-opened .vue-map-container {
  width: 580px !important;
  height: 100% !important;
  transition: all .5s ease-in;
}
.page_contacts .button__map {
  position: absolute;
  right: 2vh;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  margin: 0;
  height: 58px;
  width: 71px;
  background-color: #630bd1;
}
.page_contacts .form-group {
  margin: 0 15px 30px 15px;
}
.page_contacts .map-icon {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.page_contacts .contact-info {
  font-size: 12px;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  text-align: center;
  opacity: 0.5;
}
.page_contacts .contact-info p {
    width: auto;
    display: inline-block;
    margin: 10px 20px;
}
.page_contacts .contact-info p:nth-child(-n+2):after {
      content: '\007C';
      margin-left: 40px;
}



