
.fade-enter {
  opacity: 0;
}
.fade-enter-to {
  transition: opacity 1s ease;
  opacity: 1;
  position: fixed;
  top: 0;
  width: 100%;
}
.fade-leave {
  opacity: 1;
}
.fade-leave-to {
  transition: opacity 1s ease;
  opacity: 0;
}
.slide-left-enter {
  margin-left: 100%;
}
.slide-left-enter-to {
  transition: all 0.5s ease-in-out;
  margin-left: 0;
  position: fixed;
  top: 0;
  width: 100%;
}
.slide-left-leave {
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
}
.slide-left-leave-to {
  transition: all 0.5s ease-in-out;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.slide-right-enter {
  margin-left: -100%;
}
.slide-right-enter-to {
  transition: all 0.5s ease-in-out;
  margin-left: 0;
  position: fixed;
  top: 0;
  width: 100%;
}
.slide-right-leave {
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
}
.slide-right-leave-to {
  transition: all 0.5s ease-in-out;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.slide-top-enter {
  top: 100%;
}
.slide-top-enter-to {
  top: 0;
  transition: all 0.5s ease-in-out;
}
.slide-top-leave {
  top: 0;
}
.slide-top-leave-to {
  top: -100%;
  transition: all 0.5s ease-in-out;
}



