
.section-preloader {
  padding: 2vh;
  background: #1e1e1e;
  position: absolute;
  width: 100vw;
  z-index: 1;
}
.section-border {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 96vh;
  position: relative;
}
.svg-farnosova {
  width: 100vh;
}
.logo-letters {
  animation: letter-opacity 3s ease-in;
  -webkit-animation: letter-opacity 3s ease-in;
}
.pre-hr {
  position: absolute;
  background-color: #545454;
  -webkit-animation-duration: 3s;
  animation-duration: 3s;
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
}
.pre-hr_top {
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  -webkit-animation-name: draw-line__width;
  animation-name: draw-line__width;
}
.pre-hr_bottom {
  bottom: 0;
  right: 0;
  width: 100%;
  height: 1px;
  -webkit-animation-name: draw-line__width;
  animation-name: draw-line__width;
}
.pre-hr_left {
  top: 0;
  left: 0;
  width: 1px;
  height: 100%;
  -webkit-animation-name: draw-line__height;
  animation-name: draw-line__height;
}
.pre-hr_right {
  bottom: 0;
  right: 0;
  width: 1px;
  height: 100%;
  -webkit-animation-name: draw-line__height;
  animation-name: draw-line__height;
}
@-webkit-keyframes draw-line__width {
0% {
    width: 0;
}
100% {
    width: 100%;
}
}
@keyframes draw-line__width {
0% {
    width: 0;
}
100% {
    width: 100%;
}
}
@-webkit-keyframes draw-line__height {
0% {
    height: 0;
}
100% {
    height: 100%;
}
}
@keyframes draw-line__height {
0% {
    height: 0;
}
100% {
    height: 100%;
}
}
@-webkit-keyframes letter-opacity {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes letter-opacity {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}



