
h1, h2, h3, h4, h5, h6 {
  color: white;
  padding: 0;
  margin: 0;
}
h1 {
  font-family: 'Roboto', sans-serif;
  font-weight: 100;
  font-size: 36px;
  line-height: 60px;
  letter-spacing: 0.2em;
  text-transform: uppercase;
}
h2 {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 24px;
  line-height: 40px;
  letter-spacing: 0.2em;
  text-transform: uppercase;
}
h3 {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
}
h4 {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 15px;
  line-height: 25px;
}
p {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 15px;
  line-height: 25px;
}
.button {
  display: inline-block;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 300;
  text-align: center;
  color: white;
  letter-spacing: 0.2em;
  line-height: 30px;
  text-transform: uppercase;
  background: transparent;
  border: 1px solid transparent;
  outline: none;
  position: relative;
  overflow: hidden;
  width: 100%;
}
.button:hover, .button:active, .button:focus, .button:visited {
    cursor: pointer;
    text-decoration: none;
    outline: none;
}
.button__small {
  width: 100%;
  max-width: 210px;
  padding: 20px;
}
.button__middle {
  width: 100%;
  max-width: 320px;
  padding: 25px;
}
.button__big {
  width: 100%;
  max-width: 420px;
  padding: 25px;
}
.info-block *,
.info-block *:before,
.info-block *:after {
  box-sizing: border-box;
  transition: 0.5s ease-in-out;
  z-index: -1;
}
.info-block * {
  z-index: 40;
}
.button-wrap *,
.button-wrap *:before,
.button-wrap *:after {
  box-sizing: border-box;
  transition: 0.5s ease-in-out;
  -webkit-transition: 0.5s ease-in-out;
  z-index: -1;
}
.button__full {
  position: relative;
  display: block;
  overflow: hidden;
  width: 100%;
  height: 50px;
  line-height: 50px;
  max-width: 250px;
  text-align: center;
  text-transform: uppercase;
  z-index: 20;
  color: #fff;
  cursor: pointer;
}
.button__full:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #7317ef;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
}
.button__full:hover {
    color: #ccd4c7;
    text-decoration: none;
}
.button__full:hover:before {
    -webkit-transform: translateX(0);
    transform: translateX(0);
}
.button__full:focus {
    text-decoration: none;
    outline: 0;
}
.button__full_inverse:before {
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
}
.button__full_inverse:hover:before {
    -webkit-transform: translateX(0);
    transform: translateX(0);
}
.button-border__thin {
  border: 1px solid #363636;
}
.button-border__thin:hover {
    border: 1px solid transparent;
}
.button__slider {
  position: absolute;
  height: 95px;
  font-size: 18px;
  font-weight: 300;
  line-height: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.button-border__regular {
  border: 1px solid rgba(255, 255, 255, 0.5);
}
.button-border__regular:hover {
    border: 1px solid transparent;
}
.button__indigo:hover {
  background: #7317ef;
}
.button_arrow {
  position: relative;
}
.button_arrow .svg-line {
    width: 140px;
    padding: 20px 0;
    padding-left: 3px;
}
.button_arrow .svg-line:hover {
      width: 164px;
      padding-left: 0;
}
.button_arrow.white .svg_path-line {
    fill: #fff;
}
.button_arrow__small .svg-line {
    width: 105px;
    padding-left: 0;
}
.button_arrow__small .svg-line:hover {
      width: 112px;
}
a {
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  color: white;
  letter-spacing: 0.2em;
  line-height: 20px;
  text-transform: uppercase;
}
a:hover {
    text-decoration: underline;
}
a.link__black {
  color: black;
}
header a {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  color: white;
  text-align: left;
  letter-spacing: 0.2em;
  line-height: 60px;
}
header a:hover {
    color: #7317ef;
    text-decoration: none;
}
.white-bg {
  background: white;
}
.black-bg {
  background: black;
}
.white-text {
  color: white;
}
.black-text {
  color: black;
}
.input {
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0;
  height: 40px;
  padding: 0 12px;
  font-size: 10px;
  font-family: Roboto;
  color: white;
  letter-spacing: 0.2em;
  text-transform: uppercase;
}
.input:hover, .input:focus {
    color: #fff;
    border: 1px solid #fff;
    box-shadow: none;
}
.input::-webkit-input-placeholder {
    font-weight: 500;
}
.input::-moz-placeholder {
    font-weight: 500;
}
.input:-ms-input-placeholder {
    font-weight: 500;
}
.input:-moz-placeholder {
    font-weight: 500;
}
.input.error {
  border: 1px solid #fb4949;
  color: rgba(251, 73, 73, 0.5);
}
.input__textarea {
  max-width: 100%;
  padding: 30px 12px;
  height: 140px !important;
}
.svg-wrapper {
  position: relative;
  cursor: pointer;
  select: none;
}
.svg-wrapper .shape {
  transition: stroke-width 1s, stroke-dashoffset 1s, stroke-dasharray 1.1s, fill 0.4s;
  fill: transparent;
  stroke-width: 0.3px;
  stroke: #19f6e8;
  stroke-dasharray: 140 550 160;
  stroke-dashoffset: -474;
}
.svg-wrapper .text {
  transition: 0.8s;
  letter-spacing: 3px;
  position: absolute;
  top: 20px;
  font-family: 'Roboto Thin';
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-weight: 400;
  font-size: 22px;
  text-transform: uppercase;
  color: #22b5a5;
  text-align: center;
  width: 100%;
  height: 100%;
}
.svg-wrapper:hover .shape {
  stroke-width: 1.3px;
  stroke-dashoffset: 0;
  stroke-dasharray: 760;
}
.svg-wrapper:hover .text {
  letter-spacing: 6px;
  color: #19f6e8;
}
.svg-wrapper:active .text {
  color: #555;
  font-weight: bold;
}
.styleguide {
  background: black;
  padding: 50px 100px 300px 250px;
}
.block {
  width: 100px;
  height: 100px;
  margin: 10px;
  display: inline-block;
  border: 1px solid gray;
}
.block1 {
  background: black;
}
.block2 {
  background: white;
}
.block3 {
  background: #7317ef;
}
.block4 {
  background: #363636;
}
.block5 {
  background: #fb4949;
}



