
.slider-button {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  min-width: 320px;
}
.slider-button__right {
    right: 0;
}
.slider-button__right .button__slider {
      right: 1px;
}
.slider-button__left {
    left: 0;
}
.slider-button__left .button__slider {
      left: 5px;
}



