
.m0 {
  margin: 0;
}
.plr0 {
  padding-left: 0;
  padding-right: 0;
}
.bg_white {
  background-color: white;
  color: black;
}
.bg_white h1, .bg_white h2, .bg_white h3, .bg_white h4, .bg_white h5, .bg_white h6, .bg_white p, .bg_white div {
    color: black;
}
.bg_black {
  background-color: black;
  color: white;
}
.bg_black h1, .bg_black h2, .bg_black h3, .bg_black h4, .bg_black h5, .bg_black h6, .bg_black p, .bg_black div {
    color: white;
}
.h100 {
  height: 100%;
}
.table {
  display: table;
  height: 100%;
  margin: 0;
}
.table-cell {
  display: table-cell;
  vertical-align: middle;
  height: 100%;
}
.vam {
  vertical-align: middle;
}
.pr {
  position: relative;
}
.title-lines {
  position: relative;
  z-index: 1;
}
.title-lines:before {
    border-top: 1px solid rgba(255, 255, 255, 0.43);
    content: "";
    margin: 0 auto;
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    bottom: 0;
    width: 50%;
    z-index: -1;
}
.title-lines span {
    /* to hide the lines from behind the text, you have to set the background color the same as the container */
    background: #000;
    padding: 0 15px;
}
.container {
  padding: 0 calc(20 / 320 * 100vw);
  margin: 0;
  width: 100%;
}
.row {
  padding: 0;
  margin: 0;
}
[class*="col-"] {
  padding: 0;
}
.info_divided ul {
  list-style-type: none;
  padding: 0;
}
.info_divided ul li {
    font-size: 15px;
    margin-bottom: 20px;
    margin-left: 15px;
}
.info_divided ul li:before {
    color: #808080;
    content: '\2014';
    padding-right: 20px;
    margin-left: -35px;
}
.fixed {
  position: fixed;
}
.button__back {
  height: 50px;
  width: 95px;
  position: fixed;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  border: 0;
  text-align: center;
  text-transform: uppercase;
  font-size: 11px;
  font-weight: 500;
  letter-spacing: 0.2em;
}
.button__back-transparent {
    background: transparent;
    border: 1px solid #fff;
    color: #fff;
    transition: all .5s ease-in;
}
.button__back-transparent:hover {
      background: white;
      border: 1px solid transparent;
      color: #333;
      transition: all .5s ease-in;
}
.button__back:focus {
    outline: none;
}
.button__scroll-down {
  position: fixed;
  bottom: 30px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: block;
  font-size: 11px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  border: 0;
  letter-spacing: 0.2em;
}
.button__scroll-down__white {
    background: #fff;
}
.button__scroll-down__transparent {
    background: transparent;
    color: white;
}



