
.banner {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  height: 100vh;
}
.banner__title {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateY(-50%) translateX(-50%);
            transform: translateY(-50%) translateX(-50%);
    margin: 0;
    margin: 0;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 30px;
    color: white;
    font-size: 30px;
    text-align: center;
}



