
.page_home .info_divided p.description {
  margin-bottom: 50px;
}
.page_home .info_divided .description.contacts p {
  margin-bottom: 50px;
  letter-spacing: 0.2em;
}
.page_home__farnosova {
  position: relative;
  height: 100vh;
  background-color: darkgrey;
}
.page_home__farnosova .layer {
    position: absolute;
    top: 0;
    left: 0;
}
.page_home__farnosova .layer_image {
      background-size: cover;
      height: 100%;
      width: 100%;
}
.page_home__farnosova .layer_padder {
      background-color: #fff;
}
.page_home__farnosova .layer_padder.--top {
        top: 10px;
        left: 10px;
}
.page_home__farnosova .layer_padder.--bottom {
        top: auto;
        left: auto;
        bottom: 10px;
        right: 10px;
}
.current .draw {
  -webkit-animation: draw 2s forwards;
          animation: draw 2s forwards;
}
@-webkit-keyframes draw {
from {
    height: 0;
    width: 0;
}
to {
    height: calc(100% - 20px);
    width: calc(100% - 20px);
}
}
@keyframes draw {
from {
    height: 0;
    width: 0;
}
to {
    height: calc(100% - 20px);
    width: calc(100% - 20px);
}
}
.page_home__lastestProjectImage {
  background-image: url("/static/images/home__info-block_2.jpg");
  background-size: cover;
  height: 100vh;
}



