
#nav_global .logo {
  top: 60px;
  left: 40px;
  z-index: 50;
}
#nav_global .logo svg {
    width: 130px;
}
#nav_global .social-icons {
  bottom: 60px;
  left: 40px;
  z-index: 50;
  color: #fff;
}
#nav_global .social-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
#nav_global .social-list li {
    display: inline-block;
    padding-right: 30px;
}
#nav_global .lang-switch {
  z-index: 50;
  bottom: 60px;
  right: 40px;
}
#nav_global .menu {
  z-index: 50;
  right: 40px;
  top: 60px;
}
#nav_global .menu ul {
    list-style-type: none;
}
#nav_global .menu ul li {
      display: inline-block;
}
#nav_global .menu ul li a {
        padding: 0 20px;
}
#nav_global .menu li.active a {
    text-shadow: 1px 0px 1px #969696;
    color: #fff;
}
#nav_global a {
  color: #909090;
  cursor: pointer;
}
#nav_global a:hover {
    text-decoration: none;
}
#nav_global a:focus {
    outline: 0;
    text-decoration: none;
}



